body,h1,h2,h3,h4,h5,h6,hr,p,blockquote,dl,dt,dd,ul,ol,li,pre,form,fieldset,legend,button,input,textarea,th,td{margin:0;padding:0;font-family:"PingFangSC-Regular";outline:none
}
body{margin:0px auto !important;width:getUnit(750px);font-size:getUnit(28px);font-weight:400;background-color:#fff
}
*{color:#fff
}
h1{font-size:50px;text-align:center
}
h2{text-align:center
}
.cli-header{height:80px;width:100%;background:#00649F;line-height:80px;display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-pack:justify;-ms-flex-pack:justify;justify-content:space-between;color:#f6f6f6
}
.cli-header .logo{width:30%;margin-left:10%
}
.cli-header .nav{margin-right:10%
}
.cli-header .nav li{list-style:none;float:left;padding:0 50px;font-weight:bold;cursor:pointer
}
.cli-header .nav li a{text-decoration:none
}
.cli-main{width:100%;background:#01AAC1
}
.cli-main .intro{height:400px;padding-top:100px;color:#fff
}
.cli-main .intro h2{text-align:center;margin-top:35px
}
.cli-main .intro .cli-btn{width:150px;height:50px;margin:25px auto;display:block;text-align:center;line-height:50px;border:#f6f6f6 solid 1px;border-radius:5px;cursor:pointer
}
.cli-main .features{background:#00649F;height:auto;padding-bottom:35px;padding-top:50px
}
.cli-main .features .title{height:80px;text-align:center;font-size:35px;line-height:80px
}
.cli-main .features .list{display:-webkit-box;display:-ms-flexbox;display:flex
}
.cli-main .features .list .item-col-3{width:30%;margin:0 5%;height:180px;padding:35px;line-height:45px
}
.cli-main .features .list .item-col-3 h3{line-height:60px
}
.cli-main .issues{background:#01AAC1;padding-bottom:35px;padding-top:50px;height:auto
}
.cli-main .issues .title{height:80px;text-align:center;font-size:35px;line-height:80px
}
.cli-main .issues .list{display:-webkit-box;display:-ms-flexbox;display:flex;-ms-flex-wrap:wrap;flex-wrap:wrap;-webkit-box-pack:justify;-ms-flex-pack:justify;justify-content:space-between
}
.cli-main .issues .list .item-col-2{width:36%;margin:25px 5%;height:180px;padding:35px
}
.cli-main .issues .list .item-col-2 p{line-height:45px
}
.cli-footer{height:60px;width:100%;background:#00649F;text-align:center;line-height:60px
}

